.content__wrapper {
    display: block;
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px 15px;
}

@media (max-width: 700px) {
    .content__wrapper {
        padding: 5px;
    }
}

.content__wrapper_pagination {
    display: flex;
    justify-content: center;
    margin: 25px 10px;
}

.content__item {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    min-height: 120px;
    margin: 0px auto 15px;
    border-radius: 15px;
    background-color: #dedbd3;
    width: 80%;
    box-shadow: 0.5px 0.5px 10px rgba(0, 0, 0, .6);
    padding: 10px;
}

@media (max-width: 700px) {
    .content__item {
        width: 90%;
    }
}

@media (max-width: 650px) {
    .content__item {
        width: 98%;
    }
}

.content__item_img img {
    width: 100px;
    height: 100px;
    border: 2px solid darkgrey;
    border-radius: 50%;
}

.content__item_button_unfollow {
    border-radius: 50%;
    padding: 15px;
    border: 0;
    outline: none;
    background-color: #ebebf7;
}

.content__item_button_unfollow:disabled {
    background-color: #7b7be0;
}

.content__item_button_unfollow:hover {
    background-color: #98989e;
    transition: background-color .5s;
}

.content__item_button_follow {
    border-radius: 50%;
    padding: 15px;
    border: 0;
    outline: none;
    background-color: #ebebf7;
}

.content__item_button_follow:disabled {
    background-color: #7b7be0;
}

.content__item_button_follow:hover {
    background-color: #98989e;
    transition: background-color .5s;
}

.content__item_info {
    display: block;
    width: 400px;
    min-height: 100px;
    margin-left: 40px;
    padding: 10px;
}

@media (max-width: 650px) {
    .content__item_info {
        margin-left: 20px;
    }
}

.content__item_info_name {
    top: 5%;
    margin-bottom: 10px;
    word-break: break-word;
}

.content__item_info_status {
    top: 50%;
    word-break: break-word;
}

.page {
    cursor: pointer;
}

.active_page {
    font-weight: bold;
}


.circleProgress {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
}


/*animated button up*/
.scrollTop_wrapper{
    width: 100%;
}
.scrollTop {
     position: fixed;
     width: 100px;
     bottom: 20px;
     right: 10px;
     align-items: center;
     height: 20px;
     justify-content: center;
     z-index: 1000;
     cursor: pointer;
     animation: fadeIn 0.3s;
     transition: opacity 0.4s;
     opacity: 0.5;
 }
@media (max-width: 900px) {
    .scrollTop {
        right: 0px;
    }
}
@media (max-width: 800px) {
    .scrollTop {
        right: -10px;
    }
}
@media (max-width: 700px) {
    .scrollTop {
        left: 50px;
    }
}
@media (max-width: 600px) {
    .scrollTop {
        display: none;
        position: absolute;
    }
}

.scrollTop:hover{
    opacity: 1;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0.5;
    }
}